<!-- LoginContainer.vue -->
<template>
    <div class="login-container">
        <h2>Login</h2>
        <form @submit.prevent="handleLogin">
            <input v-model="credentials.username" type="text" placeholder="Username" required />
            <input v-model="credentials.password" type="password" placeholder="Password" required />
            <button type="submit">Log In</button>
        </form>
        <p v-if="error" class="error">{{ error }}</p>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'LoginContainer',
    data() {
        return {
            credentials: {
                username: '',
                password: ''
            },
            error: null
        };
    },
    methods: {
        async handleLogin() {
            try {
                const response = await axios.post('http://baoding.dreamsky0822.asia:10000/login', this.credentials);
                const { token } = response.data;
                localStorage.setItem('token', token); // 存储令牌
                this.$router.push('/dashboard'); // 登录成功后重定向到仪表盘
            } catch (error) {
                console.error('Login failed:', error.response ? error.response.data : error.message);
                this.error = 'Invalid credentials or server error.';
            }
        }
    },
    mounted() {

    }
};
</script>

<style scoped>
.login-container {
    max-width: 300px;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.error {
    color: red;
}
</style>