<template>
    <div id="main">
        <container-code-vue
        v-for="(component,index) in container_create"
        :key="component[0]"
        :index="index"
        :containerID="component[0]"
        :containerStatus="component[1]"
        ></container-code-vue>
        
    </div>
</template>

<script>
import axios from 'axios';

import containerCodeVue from './containerCode.vue'
export default {
    data() {
        return {
            intervalId: null,

            //容器数组验证数组
            container_get: [],

            //生成容器数组
            container_create: [],

            //容器索引id
            containercountID:0,
        }
    },
    components: {
        containerCodeVue,
    },
    mounted() {
        this.checkcontainer();
        this.intervalId = setInterval(this.checkcontainer, 5000);
    },
    methods:{
        async checkcontainer() {
            try {

                // containerimage 为索引的镜像名称 后期可修改
                const containerimage = "codeserver-wuming:0.9";
                const respones = await axios.post('http://baoding.dreamsky0822.asia:10000/getcontainerstatus', {
                    containerImage: containerimage
                });
                //进行数组接收
                this.container_get = await this.transgetcontainer(respones.data);
                console.log("container:", this.container_get);
                //整合数组，json文件转化成数组
            } catch (err) {
                console.error(err);
                console.log("网络连接错误")
            }
        },

        async transgetcontainer(jsondata) {
            //二维数组
            let responestrans = [];
            //遍历json文本
            jsondata.forEach(container => {
                responestrans.push(
                    [container.Id, container.State, container.Status]
                    //容器ID ,容器状态 ,容器整体状态
                )
            });
            //console.log("responestrans",responestrans);
            return responestrans;
            
        },

        //更新容器container
        updatecontainer(containerNewval) {
            console.log("update-container", containerNewval);
            this.container_create = [];
            //重新开始推入
            for (let i = 0; i < containerNewval.length; i++ ){
                this.container_create.push(
                     containerNewval[i]
                )
                console.log("push",containerNewval[i]);
            }
            console.log(this.container_get);
        }
    },
    props:{
        containerID: String,
        containerCount:Number,
    },
    watch:{
        containerID(newVal){
            console.log(newVal);
        },
        container_get(newVal, oldVal) {
            if (newVal.length == oldVal.length){
                // console.log("node",newVal,oldVal.length);
                //进行遍历
                let check = false;
                for (let i = 0; i < oldVal.length ; i++){
                    if (newVal[i][0] != oldVal[i][0]) {
                        //正明有值不同
                        check = true;
                    }
                }
                if (check) {
                    //更新容器
                    console.log("update");
                    this.updatecontainer(newVal);
                }
                
            } else {
                //更新容器
                console.log("update");
                this.updatecontainer(newVal);
            }
        }

    },

    
}
</script>

